*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.miandiv{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-direction: row;
    max-height: 20000vh;
    margin-top: 5.5%;
    height: 100vh ;
    background-image: url('../IMG/mainbackground.webp');
    background-size: cover;
    width: 100%;
  
}

.nav{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  
    height: 200px;
}

.search{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 30%;
    height: 100%;

  
}

.searhdiv{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(189, 34, 34);
    max-height: 100vh;
    height: auto;
    width: 63%;  
}

.resultcontainer{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    width: 30%;
    max-height: 200vh;
    height: 10vh auto;
    margin-top: -23%;
    position: fixed;
    top: 77.5%;

}

.search input{

 border: none;
 font-size: 120%;
 padding: 2%;
 
}
.category{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: row;
    width: 70%;
    height: 100%;
}

.container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 46%;
    background-color: blue;
    padding: 2%;
    border: solid 5px black;
    border-radius: 10%;
}

.container ul{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    
}

.container li{
    background-color: burlywood;
    font-size: 150%;
    font-weight: bolder;
    list-style: none;
}

.display{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    background-color: blueviolet;
    max-height: 400%;
    margin: 2%;
    width: 25%;
    height: 35% auto;
}

.productimg{
    border-radius: 5%;
    width: 30%;
}

.display h4{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bolder;
    font-size: 120%;
    padding: 2%;
    width: 100%;

}

.productform{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}