
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a{
    text-decoration: none;
}
.maindiv{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center ;
    background-color: white;
    padding: 1%;
    position: fixed;
    top: 0;
    box-shadow: .1px 0.7px 10px;
}

.logo{
    width: 50%;


    
}

.tecno{
    color: blue;
    padding: 2%; 
    font-size: 170%;
    font-family: 'Orbitron', sans-serif;
        
}

.seconddiv{
    display: flex;
    justify-content: space-evenly;
    width: 40%;
    align-items: center;
 
}

.btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 110%;
    border: none;
    padding: 2%;
    background-color: whitesmoke;
    cursor: pointer;
    font-family: 'Phudu', cursive;
    font-size: 150%;
    border-radius: 10%;

}

.logout{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 18%;
    border: none;
    background-color: whitesmoke;
    cursor: pointer;
    font-family: 'Phudu', cursive;
    font-size: 150%;
    border-radius: 10%;
}

.btn:hover{
    background-color: black;
    color: white;
}
.logout:hover{
    background-color: black;
    color: white;
}