*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.miandiv{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-direction: row;
    max-height: 20000vh;
    margin-top: 8.5%;
    height: 100vh auto;
    background-image: url('../IMG/mainbackground.webp');
    background-size: cover;
    width: 100%;
  
}

.display{
    width: 25%;
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5%;
    color:white;
    font-size: 150%;
    background-color: #934B00;
    margin: 5%;

}

.productimg{
    border-radius: 10%;
    width: 70%;
}


