*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.maindiv{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-direction: row;
    max-height: 20000vh;
    margin-top: 8.5%;
    height: 100vh auto;
    background-image: url('../IMG/mainbackground.webp');
    background-size: cover;
    width: 100%;
  
}

.firstdiv{
    max-height: 700vh;
    height: 100vh auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: gray;
    width: 100%;
}

.table{
    max-height: 7000vh;
    height: 100vh auto;
    width: 100%;
   background-color: beige;
}

.tableheader th{
    font-size: 180%;
    font-weight: bolder;
    border: solid green 3px;
    color: gray;
}

.tableData td{
    font-size: 150%;
    border: solid gray 3px;
    color: rgb(19, 92, 138);
}